import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Slide from "@mui/material/Slide";
import { IoClose } from "react-icons/io5";
import { TiArrowBack } from "react-icons/ti";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { setReload } from "../../../reduxjs/ReloadSlice";

const MarksActionContainer = React.lazy(() => import("./MarksActionContainer"));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AddMarks({ classInfo, openBox, setOpenBox }) {
  const dispatch = useDispatch();
  const [backToOption, setBackToOption] = React.useState(false);

  React.useEffect(() => {}, [openBox, backToOption]);

  const handleClose = () => {
    dispatch(setReload());
    setOpenBox(false);
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={openBox}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        {/**
         * Top Tools Bar
         */}
        <ToolsBar
          handleClose={handleClose}
          classInfo={classInfo}
          setBack={setBackToOption}
          back={backToOption}
        />
        <MarkContainer>
          <MarksActionContainer
            classInfo={classInfo}
            back={backToOption}
            setBack={setBackToOption}
          />
        </MarkContainer>
      </Dialog>
    </div>
  );
}

function ToolsBar({ handleClose, classInfo, setBack, back }) {
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <div style={{ width: "45px" }}>
          {back && (
            <IconButton
              edge="end"
              color="inherit"
              onClick={() => setBack(false)}
              aria-label="close"
            >
              <TiArrowBack />
            </IconButton>
          )}
        </div>

        <Typography
          sx={{
            ml: 2,
            flex: 1,
          }}
          variant="h6"
          component="div"
        >
          Add Marks
        </Typography>
        {/* <Button autoFocus color="inherit" onClick={handleClose}>
       save
      </Button> */}
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
          }}
        >
          {classInfo.courseName + " - Term: " + classInfo.term}
        </p>
        <Icon>
          <IconButton color="inherit" onClick={handleClose} aria-label="close">
            <IoClose />
          </IconButton>
        </Icon>
      </Toolbar>
    </AppBar>
  );
}

const MarkContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-top: 0.5rem;
`;

const Icon = styled.div`
  width: 45px;
  height: 45px;
  border: 2px solid #fff;
  border-radius: 50px;
  display: flex;
  align-items: center;
`;
